.Container {
    height: 70vh;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    font-family: 'Architects Daughter', cursive;
}

.Container input {
    margin: 10px 0;
    width: 100%;
    height: 45px;
    padding: 0 12px;
    font-family: 'Architects Daughter', cursive;
    font-size: 16px;
    letter-spacing: 0.05rem;
}

.ActionsContainer {
    margin-top: 90px;
    padding: 0;
    display: flex;
    justify-content: flex-end;
}

.ActionsContainer > * {
    padding-left: 20px;
}

.EditingArea {
    font-family: inherit;
}
